<template>
  <b-form @submit="onSubmit" class="my-5 d-block">
    <b-form-group label="Charger un fichier ">
      <b-form-file accept=".csv, .txt" v-model="fichier"></b-form-file>
    </b-form-group>
    <b-button type="submit" variant="primary"> Voir le fichier </b-button>
  </b-form>
</template>
<script>
import { mapState } from "vuex";
/**
 * definition de papa;
 */
var Papa;
if (window.Papa) {
  Papa = window.Papa;
}

export default {
  name: "input-file",
  data() {
    return {
      fichier: null,
    };
  },
  computed: {
    ...mapState(["headerFile", "rowsFile"]),
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      if (this.fichier) {
        console.log(this.fichier);
        this.readCsv(this.fichier);
      }
    },
    readCsv: function (file) {
      Papa.parse(file, {
        config: {
          // base config to use for each file
        },
        complete: (results) => {
          // executed after all files are complete
          if (results.errors.length == 0) {
            let firstRow = results.data[0];
            let headers = [];
            firstRow.forEach((label, i) => {
              headers.push({ label: label + "(" + i + ")", key: "" + i + "" });
            });
            this.$store.dispatch("setHeaderFile", headers);
            var rows = results.data;
            rows.splice(0, 1);
            this.$store.dispatch("setRowsFile", rows);
          } else {
            alert(" Erreur de lecture du fichier CSV ");
          }
        },
      });
    },
  },
};
</script>
